@charset "utf-8";
/*重置默认样式*/
/*---------------------base reset--------------------------*/
/*使其容器如DIV的内容自动换行*/
*{word-wrap: break-word;}
/*去掉内外边距-内外边距通常让各个浏览器样式的表现位置不同*/
html,body,h1,h2,h3,h4,h5,h6,hr,p,iframe,dl,dt,dd,ul,ol,li,pre,form,button,input,textarea,th,td,fieldset{
    margin:0;padding:0
}
/*谷歌支持小于12px字体的解决办法*/
html,body{-webkit-text-size-adjust:none; font-size:8px;}
/*去掉列表标签的默认样式*/
ul,ol,dl{list-style-type:none}
/*防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */
html{
    -webkit-text-size-adjust:100%; /*禁用Webkit内核浏览器的文字大小调整功能，默认是auto；*/
    -ms-text-size-adjust:100%; /*禁用IE内核浏览器的文字大小调整功能，默认是auto；*/
    font-family: sans-serif; /* 1 */
    -webkit-font-smoothing: antialiased;
    font-size:625%;  /*所有的px尺寸/100*/
}
address,caption,cite,code,dfn,em,th,var,i,em{font-style:normal;font-weight:normal}
input,button,textarea,select,optgroup,option{font-family:inherit;font-size:inherit;font-style:inherit;font-weight:inherit}
button{overflow: visible;vertical-align:middle;outline:none}
body,th,td,button,input,select,textarea{
    letter-spacing: 0;
    font-family:Arial,PingFangSC-Semibold,'\5FAE\8F6F\96C5\9ED1',"Microsoft Yahei","\5B8B\4F53","Microsoft Yahei","Hiragino Sans GB","Helvetica Neue",Helvetica,tahoma,arial,Verdana,sans-serif,"WenQuanYi Micro Hei","\5B8B\4F53";
    font-size:14px;
    -webkit-font-smoothing:antialiased;
    -moz-font-smoothing:antialiased
}
body{
    font-size:.14rem;
    color:#333;
    background:#fff;
}
hr{
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    height: 0;
}
h1,h2,h3,h4,h5,h6{font-size:100%}
a,area{outline:none;blr:expression(this.onFocus=this.blur())}
a{text-decoration:none; color:#333;}
a:hover{text-decoration:none; /*color:#fff !important;*/}
a:focus{outline:none;text-decoration:none;}
a:hover,a:active{outline:none; /*color:#51B44F;*/}
:focus{outline:none}
sub,sup{vertical-align:baseline}
/* li, span, p{transition:all 0.2s linear 0s;} */
/*img自适应*/
.img-responsive{display:block; max-width:100%; height:auto;}
.img-responsive2{display:block; width:100%; height:auto;}
/*IE下a:hover 背景闪烁*/
html{overflow:-moz-scrollbars-vertical;zoom:expression(function(ele){ele.style.zoom = "1";document.execCommand("BackgroundImageCache",false,true)}(this))}
img{border:none;}
/*图片居右*/
.alignleft {
    display: inline;
    float: left;
}
.alignright {
    display: inline;
    float: right;
}
.aligncenter {
    clear: both;
    display: block;
    margin:auto;
}
/*---------------------HTML5 reset--------------------------*/
header,footer,section,aside,details,menu,article,section,nav,address,hgroup,figure,figcaption,legend{display:block;margin:0;padding:0}
time{display:inline}
audio,canvas,video{display:inline-block;*display:inline;*zoom:1}
audio:not([controls]){display:none}
svg:not(:root) {overflow: hidden}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button {height: auto}
input[type="search"] {-webkit-appearance: textfield; /* 1 */-webkit-box-sizing: content-box; /* 2 */box-sizing: content-box}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration {-webkit-appearance: none}
/* HTML5 媒体文件跟 img 保持一致 */
audio,canvas,video {
    display: inline-block;*display: inline;*zoom: 1;
}
/*HTML5新模块元素在IE8、9版本浏览器中没有被定义默认样式。为解决该问题，给下列元素添加“block”显示属性*/
article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display: block;}
/*清除浮动*/
.clearfix{*zoom:1;}
/*解决ie6/7下面的浮动塌陷问题*/
.clearfix:before,.clearfix:after{display:table; line-height:0; content:"";}
/*display:table;解决其他下面的浮动塌陷问题,line-height是将添加的内容高度设为0；*/
.clearfix:after{clear:both;}
/*清楚浮动*/
/*隐藏*/
.hidden{display:none;}
/*浮动*/
.fl{float:left; display:block;}
.fr{float:right; display:block;}
span{
    display: inline-block;
}
